<template>
  <div class="container">
    <div class="post-detail d-flex">
      <div class="post-detail__left-col">
        <div class="post-detail__left-col__tool-bar">
          <span class="post-detail__left-col__tool-bar__time-now">{{ currentTime }}</span>
        </div>
        <div class="post-detail__left-col__title">
          <h1>Ferdinand: 'Solskjaer nên ngẩng cao đầu rời Man Utd'</h1>
        </div>
        <div class="post-detail__left-content">
          <p>Không muốn tự dối lòng, cựu trung vệ Rio Ferdinand thừa nhận rằng đồng đội cũ Ole Gunnar Solskjaer không thể giúp Man Utd cạnh tranh danh hiệu.</p>
          <p>
            "Tôi nghĩ Solskjaer nên rời đi bây giờ trong thế ngẩng cao đầu, bởi từ khi cậu ấy đến so với thời điểm bắt đầu mùa giải này, tôi nghĩ mọi thứ là tích cực", Ferdinand nói trên kênh podcast
            <i>Five with Vibe</i>. "Man Utd khởi đầu mùa giải với rất nhiều kỳ vọng. Cùng với kỳ chuyển nhượng đã có, tất cả đều nghĩ rằng 'Đây là thời điểm để Man Utd vô địch'. Solskjaer được đưa tới
            đây, để giúp chúng tôi làm điều đó. Cậu ấy có nhiệm vụ lắp ghép đội hình, mang về những cầu thủ để giúp Man Utd cạnh tranh danh hiệu. Nhưng giờ, tôi không nghĩ rằng Man Utd có thể thách
            thức ngôi vương Ngoại hạng Anh".
          </p>
          <p>
            <img
              src="https://i1-thethao.vnecdn.net/2021/11/09/ferdinand-solskjaer-770x471-1556-1636426126.jpg?w=680&amp;h=0&amp;q=100&amp;dpr=1&amp;fit=crop&amp;s=JcaDhVMn0TJHpzLKxze_vQ"
              alt="Ferdinand (trái) từng là đồng đội của Solskjaer ở Man Utd khoảng năm năm (giai đoạn 2002-2007). Ảnh: Reuters"
            />
          </p>
          <p>Ferdinand (trái) từng là đồng đội của Solskjaer ở Man Utd khoảng năm năm (giai đoạn 2002-2007). Ảnh: <i>Reuters</i></p>
          <p>
            <a href="https://vnexpress.net/chu-de/rio-ferdinand-823">Ferdinand </a>cũng chê <a href="https://vnexpress.net/chu-de/ole-gunnar-solskjaer-768">Solskjaer </a>không xây dựng được bản sắc
            riêng cho <a href="https://vnexpress.net/chu-de/manchester-united-116">Man Utd</a>, dù được ban lãnh đạo hậu thuẫn, tuyển mộ
            <a href="https://vnexpress.net/chu-de/cristiano-ronaldo-194">Cristiano Ronaldo</a>, <a href="https://vnexpress.net/chu-de/raphael-varane-373">Raphael Varane</a> và
            <a href="https://vnexpress.net/chu-de/jadon-sancho-982">Jadon Sancho</a> với tổng giá trị 200 triệu USD hè 2021. Cựu trung vệ người Anh nói tiếp: "Tôi theo dõi CLB mỗi tuần và tự hỏi họ sẽ
            làm gì về mặt chiến thuật? Tôi không thấy bất kỳ triết lý hay bản sắc nào trong lối chơi của Man Utd, mà điều đó đáng lẽ phải có từ ban lãnh đạo. Tôi cảm thấy bối rối khi xem họ thi đấu.
            Tôi luôn hoài nghi, về việc liệu Solskjaer có thể đưa Man Utd trở thành nhà vô địch không? Tôi không bị thuyết phục, tôi không chắc cậu ấy có thể làm điều đó. Với những cầu thủ được mang
            về, và những màn trình diễn từ đầu mùa, tôi nghĩ rằng Solskjaer nên trao ngọn đuốc cho người có thể vực dậy Man Utd lúc này".
          </p>
          <p>
            Mùa này, Man Utd đã đá 11 trận Ngoại hạng Anh nhưng chỉ thắng năm và thua tới bốn – trong đó có
            <a href="https://vnexpress.net/bong-da/man-utd-vs-liverpool-4376277-tong-thuat.html">thảm bại 0-5 trước Liverpool</a> và mới nhất là
            <a href="https://vnexpress.net/bong-da/man-utd-vs-man-city-4382321-tong-thuat.html">trận thua toàn diện 0-2</a> trước đại kình địch Man City. Họ hiện tụt xuống thứ sáu với 17 điểm, kém đầu
            bảng Chelsea tới chín điểm. Trước đó, thầy trò Solskjaer đã bị <a href="https://vnexpress.net/bong-da/man-utd-bi-loai-khoi-cup-lien-doan-4360597.html">loại sớm khỏi Cup Liên đoàn</a>, và
            chưa chắc suất đi tiếp tại Champions League khi mới thắng hai trên bốn trận của vòng bảng. Áp lực vì vậy lại đè lên Solskjaer - người đã dẫn dắt CLB ba năm nhưng chưa giành được danh hiệu
            nào.
          </p>
          <p>
            Theo Ferdinand, <strong>một trong những vấn đề lớn nhất của Man Utd là Solskjaer không có đủ quyền uy trong phòng thay đồ</strong>. Ông phân tích: "Tôi xem Man Utd, và cảm thấy các cầu thủ
            không sợ HLV. Họ thi đấu thiếu cường độ, không tăng tốc và đôi khi tự làm tổn thương chính mình trong các trận đấu. Điều dễ nhận thấy là sự thiếu tôn trọng với Solskjaer và các cầu thủ
            dường như không sợ cậu ấy. Các HLV vĩ đại đều có yếu tố sợ hãi. Như Pep Guardiola, Jurgen Klopp, Thomas Tuchel bây giờ, hay Sir Alex Ferguson, George Graham hay Arsene Wenger của Arsenal
            trước đây. Họ có quyền uy buộc các cầu thủ phải thi đấu tốt, nếu không sẽ bị đày lên ghế dự bị. Tôi không thấy sự sợ hãi đó trong đội hình của Man Utd".
          </p>
          <p>
            "Tôi đã nói điều đó trước đây. Liệu một ngôi sao lớn như David Beckham hay nhà sưu tập danh hiệu Ryan Giggs có thể đi bộ về phòng ngự hay không. Điều đó không bao giờ xảy ra vì họ biết HLV
            sẽ phản ứng thế nào trong phòng thay đồ. Tôi vẫn nghĩ Man Utd ở một vị thế mà Solskjaer rời đi thì tốt hơn. Liệu cậu ấy có thể dẫn dắt một đội khác không? Tôi nghĩ là có. Sau khi chia tay
            Chelsea, Frank Lampard nhận được nhiều lời đề nghị. Bạn có thể nói 'Solskjaer là đồng đội cũ, và tôi không nên nói cậu ấy đã hết thời ở Man Utd'. Nhưng tôi phải thành thật bởi nếu đó là
            người khác, tôi cũng sẽ nói tương tự", Ferdinand nói thêm.
          </p>
          <p>
            Từng là tượng đài ở Man Utd, Solskjaer được bổ nhiệm năm 2018 với hy vọng chấn hưng sân Old Trafford. Mùa trước, ông đưa đội bóng đến vị trí á quân Ngoại hạng Anh và Europa League, bên
            cạnh việc vào bán kết Cup Liên đoàn Anh và tứ kết Cup FA.
          </p>
          <p>
            "Solskjaer làm tròn nhiệm vụ khi tới Man Utd. Cậu ấy được đưa tới đây để đem tới hy vọng cho người hâm mộ, kéo họ trở lại sân và theo dõi CLB. Solskjaer làm tốt ở khía cạnh đó, nhưng liệu
            cậu ấy có thể giúp Man Utd giành được danh hiệu không? Solskjaer có thể giúp Man Utd cạnh tranh ngôi vô địch Champions League? Tôi phải thành thật với chính mình và nói rằng, tôi không tin
            điều đó", Ferdinand nhấn mạnh.
          </p>
          <p>Lần gần nhất Man Utd đoạt danh hiệu là mùa 2016-2017. Khi đó, dưới trướng HLV Jose Mourinho, họ thắng Ajax 2-0 trong trận chung kết Europa League.</p>
        </div>
      </div>
