      currentTime: "",
    };
  },
  created() {
    this.GetCurrentTime();
  },

  methods: {
    GetCurrentTime() {
      let timeStr = moment(new Date()).locale("vi").format("dddd, D/MM/yyyy, hh:mm");
      timeStr = timeStr.substr(0, 1).toUpperCase() + timeStr.substr(1).toLowerCase();
      this.currentTime = timeStr;
    },
  },
};
</script>
